.footer_outer-container {
  background-color: #111;
  color: grey;
  display: flex;
  justify-content: center;
}

.footer_inner-container {
  width: 60%;
  /* background-color: red; */
}

.footer_icons {
  /* background-color: blue; */
  width: 12%;
  display: flex;
  justify-content: space-around;
  margin-left: 10px;
  color: #fff;
}

.footer_data {
  display: flex;
  justify-content: space-around;
  color: grey;
}

.footer_data ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 12vh;
  /* background-color: blue; */
}

.footer_data ul li {
  cursor: pointer;
}

.service_code {
    display: flex;
    align-items: center;
    justify-content: center;
    border: #fff 1px solid;
    width: 10%;
    margin: 10px 0;
    cursor: pointer;
    color: grey;
}

.copy-write {
    font-size: 12px;
    margin-bottom: 10px;
    color: grey;

}